<template>
    <div class="createRegiment">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">创建哼哈团</span>
            <span class="back"></span>
            <span class="about">说明</span>
        </div>
        <form class="regimentInfo">
            <div class="title">哼哈团头像</div>
            <div class="upload">
                <label @click="getImg">
                    <img src="../../assets/img/上传头像.png">
                </label>
                <input class="uploadImg" type="file" ref="image" accept="image/*" @change="uploadImg">
                <span>上传头像</span>
            </div>
            <div class="title">哼哈团名称</div>
            <input class="info" placeholder="为您的哼哈团取一个名字吧~">
            <div class="title">常驻地址</div>
            <input class="info" placeholder="标记常驻地址,更加方便团员找到组织哦~">
            <div class="title">哼哈团名称</div>
            <textarea class="info" style="resize: none;height: 6rem;" placeholder="简单的介绍一下您的哼哈团,方便更多用户了解您的哼哈团"></textarea>
            <div class="regimentSetting">
                <div class="regimentType">
                    <span class="regimentTitle">团员是否交费</span>
                    <span class="changeType" @click="changeType">{{ paymentType === 'month' ? '按月' : '按年'}}
                        <span class="tabImg">
                            <img src="../../assets/img/tab.png">
                        </span>
                    </span>
                    <span class="select">
                        <span class="yes">
                            <img :src="pay ? selected : unSelected" @click="selectType">
                            <span>是</span>
                        </span>
                        <span class="no">
                            <img :src="!pay ? selected : unSelected" @click="selectType">
                            <span>否</span>
                        </span>
                    </span>
                </div>
                <div class="payCounts" v-if="pay">
                    <input class="count" type="number">
                    <label class="unit">元</label>
                    <span class="notice">填写您想获得的哼哈团费用</span>
                </div>
                <button class="ensure" @click="ensure">确认创建</button>
                <div class="tips">温馨提示:创建哼哈团需要花费1000哼哈值,且需在24小时内使团队成员不少于10人</div>
            </div>
        </form>
    </div>
</template>

<script>
export default {
  name: 'createRegiment',
  data () {
    return {
      paymentType: 'month',
      pay: true,
      selected: require('../../assets/img/typeSelected.png'),
      unSelected: require('../../assets/img/typeUnSelected.png')
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getImg () {
      this.$refs.image.click()
    },
    uploadImg () {
      console.log(1)
    },
    changeType () {
      if (this.paymentType === 'month') {
        this.paymentType = 'year'
      } else {
        this.paymentType = 'month'
      }
    },
    selectType () {
      this.pay = !this.pay
    },
    ensure () {
      console.log('创建')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    color: #333333;
    font-size: 0.48rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 999;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .about {
    position: absolute;
    right: 0;
    font-size: 0.42667rem;
  }

  .regimentInfo {
    margin-top: 1.5rem;
  }

  .title {
    height: 1.14667rem;
    background: #f5f5f5;
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
    font-size: 0.42667rem;
  }

  .upload {
    width: 1.6rem;
    margin: 0 auto;
    margin-top: 0.48rem;
  }

  .upload span {
    font-size: 0.4rem;
    color: #666666;
  }

  .uploadImg {
    display: none;
  }

  .info {
    width: calc(100% - 1rem);
    padding: 0.4rem 0.5rem;
    border: none;
    outline: none;
    font-size: 0.37333rem;
  }

  .regimentSetting {
    padding: 0.4rem 0.4rem 2rem 0.4rem;
    background: #f5f5f5;
  }

  .regimentType {
    display: flex;
    align-items: center;
  }

  .regimentTitle {
    font-size: 0.4rem;
  }

  .changeType {
    display: flex;
    background: #ffffff;
    font-size: 0.37333rem;
    padding: 0.1rem 0.2rem;
    align-items: center;
    margin-left: 0.29333rem;
    border-radius: 0.3rem;
  }

  .tabImg {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 0.4rem;
    height: 0.4rem;
    margin-left: 0.2rem;
  }

  .select {
    margin-left: 0.4rem;
    position: relative;
  }

  .select,
  .select span {
    display: flex;
  }

  .select span {
    align-items: center;
    justify-content: center;
  }

  .no {
    margin-left: 1.5rem;
  }

  .select span img {
    width: 0.5rem;
    height: 0.5rem;
  }

  .select span span {
    margin-left: 0.2rem;
  }

  .count {
    border: 1px solid #ffd200;
    border-radius: 0.2rem;
    outline: none;
    width: 2.98667rem;
    height: 1.33333rem;
    margin-left: 4.8rem;
    margin-top: 0.4rem;
    text-align: center;
  }

  .unit {
    font-size: 0.42667rem;
    color: #333333;
    margin-left: 0.24rem;
    margin-top: 0.4rem;
  }

  .payCounts {
    display: flex;
    align-items: center;
    position: relative;
  }

  .notice {
    display: block;
    position: absolute;
    font-size: 0.34667rem;
    color: #666666;
    left: 4.8rem;
    top: 2rem;
  }

  .ensure {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    height: 1.33333rem;
    background: #ffd200;
    border-radius: 0.15rem;
    outline: none;
    border: none;
    color: #fff;
    margin-top: 1rem;
  }

  .tips {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    text-align: start;
    font-size: 0.37333rem;
    color: #666666;
    margin-top: 0.2rem;
  }
</style>



